.cdr-link {
  cursor: pointer;
  display: inline;
  border: none;
  text-decoration: underline;
  outline: none;
  background-color: transparent;
  border-color: inherit;
  color: #0066cc;
}
.cdr-link:hover,
  .cdr-link:active,
  .cdr-link:focus {
    outline: none;
    text-decoration: underline;
    background-color: transparent;
    color: #0066cc;
  }
.cdr-link:focus {
    outline: thin dotted #8dd1ed;
  }
.cdr-link--contrast,
  .cdr-link--contrast:hover,
  .cdr-link--contrast:active,
  .cdr-link--contrast:focus,
  .cdr-link--contrast:visited {
    color: #4eaaff;
  }
.cdr-link--overlay,
  .cdr-link--overlay:hover,
  .cdr-link--overlay:active,
  .cdr-link--overlay:focus,
  .cdr-link--overlay:visited {
    color: #ffffff;
  }
.cdr-link--standalone {
    text-decoration: none;
  }
.cdr-link--standalone:hover,
    .cdr-link--standalone:active,
    .cdr-link--standalone:focus,
    .cdr-link--standalone:visited {
      text-decoration: underline;
    }



