.cdr-radio {
  position: absolute;
  opacity: 0;
}
.cdr-radio:checked + .cdr-radio__label::before {
      background-color: black;
    }
.cdr-radio:checked + .cdr-radio__label::after {
      background-color: white;
    }
.cdr-radio[disabled] + .cdr-radio__label {
    color: gray;
    cursor: default;
  }
.cdr-radio[disabled] + .cdr-radio__label::before {
      background-color: lightgray;
    }
.cdr-radio:focus + .cdr-radio__label::before {
    -webkit-box-shadow: 0 0 3px 3px #83c4ff;
            box-shadow: 0 0 3px 3px #83c4ff;
  }
.cdr-radio__label {
    position: relative;
    margin-bottom: 0;
    line-height: 1;
  }
.cdr-radio__label:hover {
      cursor: pointer;
    }
.cdr-radio__label::before {
      content: '';
      width: 1.6rem;
      height: 1.6rem;
      border: 1px solid gray;
      -webkit-border-radius: 50%;
              border-radius: 50%;
      margin-right: 10px;
      display: inline-block;
      vertical-align: text-top;
      background-color: white;
    }
.cdr-radio__label::after {
      content: '';
      position: absolute;
      left: 0.53333rem;
      top: 0.53333rem;
      width: 0.53333rem;
      height: 0.53333rem;
      -webkit-border-radius: 50%;
              border-radius: 50%;
    }



