.cdr-list {
  list-style-type: disc;
  padding-left: 1.6rem;
}
.cdr-list > li {
    padding-bottom: 1.6rem;
  }
.cdr-list > li > ul,
    .cdr-list > li > ol {
      padding-top: 1.6rem;
      padding-left: 1.6rem;
    }
.cdr-list > li > ul > li:last-of-type, .cdr-list > li > ol > li:last-of-type {
        padding-bottom: 0;
      }
.cdr-list > li:last-of-type {
      padding-bottom: 0;
    }
.cdr-list--unstyled {
    list-style-type: none;
    padding-left: 0;
  }
.cdr-list--bulleted {
    list-style-type: disc;
  }
.cdr-list--inline {
    list-style-type: none;
    padding: 0;
  }
.cdr-list--inline > li {
      display: inline-block;
    }
.cdr-list--inline.cdr-list--bulleted > li {
      padding-right: 0;
    }
.cdr-list--inline.cdr-list--bulleted > li:not(:last-of-type)::after {
        vertical-align: text-center;
        content: "\2022";
        display: inline-block;
        color: inherit;
        margin-left: 0;
        width: 0.8rem;
      }
.cdr-list--numbered {
    list-style-type: decimal;
  }
.cdr-list--menu {
    list-style-type: none;
    padding-left: 0;
  }
.cdr-list--menu > li {
      border-bottom-width: 0.1rem;
      border-bottom-style: solid;
      border-color: #222;
      padding: 0;
    }
.cdr-list--menu > li > a {
        font-family: "Roboto Condensed", "Roboto", "Helvetica Neue", Helvetica, Arial, sans-serif;
        font-weight: 700;
        font-size: 1.8rem;
        padding: 1.1rem 1.6rem;
        color: #000;
        display: inline-block;
      }



